
.first-page {
    height: 100vh;
    background-color: rgb(24, 24, 24);

    .home-page {
        display: grid;
        height: 400px;
        grid-template-columns: 3fr 3fr;
        color: rgb(252, 252, 251);
    
        .left-side {
            display: flex;
            flex-direction: column;
            justify-content: start;
            align-items: center;
            font-family: sans-serif;
            
            h1 {
                font-size: 40px;
            }
            h3 {
                font-size: 20px;
                color: rgb(89, 89, 232);
                font-weight: 300;
                align-items: center;
                justify-content: center;
            }
            button {
                width: 230px;
                height: 45px;
                font-size: 20px;
                color: white;
                border: none;
                background-color: rgb(31, 30, 30);
            
                margin-right: 10px;
                cursor: pointer; 
                border-radius: 50px;
                background-color:rgb(82, 85, 238) ;
    
                &:hover {
                    color: rgb(0, 250, 0);
                }
            }
    
            
            
        }
        .img-side {
            display: flex;
            align-items: center;
            justify-content: center;
            img {
                box-shadow: 0px 10px 10px rgba(2, 2, 0, 0.4);
                width: 450px;
                height: 300px;
            }
        }
    }

    .svg-about {
    margin-top: 8em;
        height: 270px;

        ul {
            display: grid;
            grid-template-columns: 1fr 1fr 1fr;
            align-items: center;
            justify-content: center;
            gap: 150px;
          
    

            li {
                font-family: sans-serif;
                display: flex;
                align-items: center;
                flex-direction: column;
                color: white;
                font-size: 20px;
                list-style-type: none;

                
            
            }

        }

    }


}

