.scalping-app {
    color: white;
    background-color: #131722;
    height: 100vh;
    display: flex;

    .chart-container {
        width: 50vw;
        height: 100vh;


        .app-header-chart {
            font-family: sans-serif;
            border-top: 1px solid white;
            height: 30px;
            display: flex;
            top: 0;

            span {
                display: flex;
                color: rgb(198, 197, 197);
                align-items: center;

                justify-content: center;
                font-size: 15px;
                margin-left: 10px;
                cursor: pointer;

                &:hover {
                    color: white;
                }
            }
        }

        .under-container {
            width: 50vw;

            .chart {
                height: max-content;
                border: 1px solid white;
            }

            .side-bar {
                display: flex;
                flex-direction: column;
                border-top: 1px solid white;

                .order-book-section {
                    display: grid;
                    grid-template-columns: 2fr 2fr;
                }

                .order-book {
                    display: flex;
                    flex-direction: column;
                    margin: 5px;

                    .order-header {
                        font-family: sans-serif;
                        display: grid;
                        grid-template-columns: 0fr 1fr 2.1fr;
                        font-size: 12px;
                        // border-bottom: 1px solid white;
                        // border-left: 1px solid white;

                        span {

                            text-align: center;
                        }

                    }

                    .order-container {

                        display: flex;
                        font-size: 12px;
                        flex-direction: column;
                        overflow-y: auto;

                        .order-250 {
                            background-color: rgba(255, 166, 0, 0.523);
                        }

                        .order-500 {
                            background-color: rgba(65, 80, 180, 0.551);
                        }

                        .order-1M {
                            background-color: purple;
                        }

                        .price-side {}

                        .order-side {
                            display: none;
                        }

                        .sell-orders {
                            display: grid;


                            background-color: rgba(240, 15, 15, 0.216);

                            .sell-render-orders {


                                display: grid;
                                grid-template-columns: 3fr 2fr;
                            }
                        }

                        .buy-orders {
                            background-color: rgba(0, 255, 89, 0.207);

                            .buy-render-orders {

                                display: grid;
                                grid-template-columns: 3fr 2fr;
                            }
                        }


                    }
                }


            }
        }

        .statistic {
            display: flex;
            flex-direction: row;
            border: 1px solid gold;
            padding: 4px;
            align-items: center;
            justify-content: center;
            font-family: sans-serif;


            .statistic-buy {
                display: flex;
                width: 100%;
                flex-direction: column;
                align-items: center;
                justify-content: center;
                background-color: rgba(0, 255, 89, 0.207);

            }

            .statistic-sell {
                display: flex;
                width: 100%;
                align-items: center;
                justify-content: center;
                flex-direction: column;
                background-color: rgba(240, 15, 15, 0.216)
            }
        }
    }

    .wallet-order-info {
        margin-left: 50px;
        width: 500px;

        h1 {
            margin: 0;
            padding: 0;
        }
    }

    .bot {


        table {
            width: 100%;
            background-color: rgb(17, 53, 86);

            th,
            td {
                border: 1px solid white;
                width: 16.6%;
            }

        }
    }
}

