* {
	box-sizing: border-box;
}

html {
	line-height: 1.6;
	overflow-x: hidden;
}

body {
	font-size: 18px;
	background-color: black;
	margin: 0;
	height: 100vh;

	.tooltip {
		display: flex;
		// width: 71px;
		// height: 26px;
		font-family: sans-serif;
		font-size: 12px;
		position: absolute;
		align-items: center;
		justify-content: center;
		background-color:rgb(79, 89, 108) ;
		color: rgba(245, 245, 245, 0.822);
		padding: 5px;
		border-radius: 3px;
		visibility: hidden;
		// top: 43px;
		// left: 5px;
		transition: visibility 0.15s, opacity 2s linear;
		z-index: 1;
	}

	.tooltip-container:hover .tooltip {
		visibility: visible;
	}

}