
.search-cotainer {
    height: 100vh;
    width: 325px;
    // overflow-x: auto;
    border: 1px solid white;

    .icon-top-bar {
        display: grid;
        grid-template-columns: 1fr 5fr;
        position: sticky;
        border-bottom: 1px solid rgb(39 46 58);
        justify-content: center;
        align-items: center;
        top: 0;
        background-color: #131722;
        height: 50px;

        .tooltip {
            width: 65px;
            height: 23px;
            top: 43px;
            left: 5px;
           
        }


        .icon1 {
            margin-top: 10px;
            fill: gold;
        }

        svg {
            fill: rgb(238, 242, 247);
            margin-top: 10px;
            margin-left: 10px;

        }

    }

    .search-bar {
        background-color: #131722;
        width: 100%;
        height: max-content;

        color: black;
        height: max-content;



        .render-pairs {
            padding: 5px;
            margin-top: 0px;
            list-style-type: none;
            height: max-content;


            .pagination{
                display: flex;
                gap: 123px;
                width: 100%;
                border-top: 1px solid rgb(39 46 58);
                justify-content: center;
                padding: 10px;
                color: whitesmoke;

                span {
                    cursor: pointer;


                }
            }
           
           .pairs-volume {

            color: rgb(164, 162, 162);
            margin-right: 5px;

           }
           
           .filter-li {

            display: grid;
            height: 20px;
            grid-template-columns: 7fr 3fr 3fr;
            border: none;
            background-color: #131722;
            color: rgb(174, 173, 173);

            .tooltip {

                width: 85px;
                height: 25px;
                top: 100px;
                left: 1052px;

            }

            .contracts {
                margin-left: 0px;
            }

            .change {
                margin-left: -4px;

                &:hover {
                    color:white
                }
            }

            .volume {
                margin-left: -2px;
                &:hover {
                    color:white
                }
            }
        

                &:hover {
                    background-color: #131722;
                }

                &:active {

                    background-color: #131722;
                }
            }
            



            li {
                display: grid;
                grid-template-columns: 1fr 7fr 3fr 3fr;
                font-family: sans-serif;
                font-size: 13px;
                cursor: pointer;

                margin: 0px;
                padding-left: 5px;
                align-items: center;
                justify-content: center;

                height: 45px;
                background-color: #131722;
                color: white;

                .change-24h {
                    color: rgb(33, 165, 88);

                    &.negative {
                        color: rgb(232, 45, 59);
                    }
                }

                &:hover {

                    background-color: rgb(52, 61, 77);

                    .pairs-volume{
                         color: white;
                    }
                
                    
                }

                &:active {

                    background-color: rgb(89, 103, 129);
                }

                svg {
                    color: rgb(174, 173, 173);
                    margin-top: 0px;
                    margin-right: 5px;
                    display: flex;
                    align-items: center;
                    justify-content: center;


                }

            }
        }
    }
}

