.app-header-sections {
      display: flex;
      flex-direction: row;
      background-color: rgba(31, 30, 30, 0.886);
      top: 0;
      left: 0;
      position: sticky;
      justify-content: flex-end;
      align-items: center;
      width: 100%;
      height: 70px;
     
      z-index: 1;  

      .nav-link {
        text-decoration: none;
      }
      
      /* Если вы хотите убрать подчеркивание только при наведении на ссылку */
      .nav-link:hover {
        text-decoration: none;
      }


      .lang-filter {
        margin-right: -40px;
        font-size: 16px;
        cursor: pointer;
      }

      .header-login {
        width: 130px;
        height: 40px;
        font-size: 20px;
        color: white;
        border: 1px solid rgb(82, 85, 238) ;
        background-color: rgb(31, 30, 30);
        margin-left: 55px;
        margin-right: 10px;
        cursor: pointer; 
        border-radius: 20px;

        &:hover {
            background-color:rgb(82, 85, 238) ;
        }
      }


      span {
        color: white;
        font-family: sans-serif;
        font-size: 20px;
        margin-left: 55px;
        align-items: center;
        justify-content: center;
        padding: 15px;
        cursor: pointer;
        
        &:hover {
            color:rgb(82, 85, 238);
         
        }
      }


}