.search-bar {
    width: 100%;
    border: none;

    .search-icon {
		position: absolute;
		left: 50px;
		top: 49%;
		transform: translateY(-50%);
		fill: hsla(0, 0%, 100%, 0.7);
		transition: color 0.3s;
	}


    .search-bar:focus {
        outline: none;
        border: none;

    }
    input {
        background-color:rgb(63, 73, 91) ;
        color: white;
        padding-left: 30px;
        height: 30px;
        width: 96%;
        border-radius: 5px;
        border: none;

        
    }
    input:focus {
        outline: none;
        border: 1px solid;
        border-color:  rgb(89, 103, 129);
 
    }
}